import { ReactElement, useState } from 'react';
import './App.scss';
import Smart from './Smart';
import Stupid from './Stupid';

const App = (): ReactElement => {
  const [stupid, setStupid] = useState(true)

  return (
    <div className="App">
      <button onClick={() => setStupid(!stupid)}>{stupid ? 'Go to Normal Mode' : 'Go to ST Mode'}</button>
      {stupid ? <Stupid /> : <Smart />}
      <br></br>
      <br></br>
      <br></br>
      <br></br>
      <i>Hastily hacked up in response to this <a href="https://www.straitstimes.com/singapore/health/dine-in-group-size-cut-to-2-from-july-19-as-spore-tightens-covid-19-rules-but-up-to">ST Infographic</a></i>
      <br></br>
      <br></br>
      <br></br>
      <br></br>
    </div>
  );
}

export default App;
