import { ChangeEvent, ReactElement, useState } from 'react';
import child from './images/child.png';
import adult from './images/adult.png';
import legend from './images/legend.png';
import can from './images/can.png';
import cannot from './images/cannot.png';
import fullyVaccinated from './images/fullyVaccinated.png';
import partiallyVaccinated from './images/partiallyVaccinated.png';
import notVaccinated from './images/notVaccinated.png';
import recovered from './images/recovered.png';
import negativeTest from './images/negativeTest.png'

enum VaccinationStatus {
  None = 'none',
  Partially = 'partially',
  Fully = 'fully',
  Recovered = 'recovered',
  NegativeTest = 'negative test'
}

interface Person {
  isChild: boolean;
  vaccinationStatus: VaccinationStatus
}

const Stupid = (): ReactElement => {
  const personDefault = {
    isChild: false,
    vaccinationStatus: VaccinationStatus.None,
  };
  const [people, setPeople] = useState<Array<Person>>([personDefault, personDefault])

  console.log('ppl', people)

  const addPerson = () => {
    const newPeople = people.concat(personDefault);
    setPeople(newPeople);
  }

  const removePerson = () => {
    if (people.length === 1) return;
    const newPeople = people.slice(0, -1);
    setPeople(newPeople);
  }

  const numDiners = people.length
  const isEveryAbove12FullyVaccinatedOrEligible = people.filter(p => p.isChild === false)
    .every(person => {
      return person.vaccinationStatus !== VaccinationStatus.None
    });
  // child def as 12 or below
  const numChildren = people.filter(p => p.isChild === true).length;
  const [areDinersSameHouseHold, setAreDinersSameHousehold] = useState(true);

  const whyCannot = () => {
    if (numDiners >= 15) {
      return 'You have more than 5 people dining in. Oi, stop adding. cannot la. LOL. ok sure. whatever';
    }
    if (numDiners >= 10) {
      return 'You have more than 5 people dining in. Oi, stop adding. cannot la. ';
    }
    if (numDiners >= 6) {
      return 'You have more than 5 people dining in';
    }
    if (!isEveryAbove12FullyVaccinatedOrEligible) {
      return 'Not everyone above 12 is (1) fully vaccinated or (2) has negative test or (3) recently recovered from COVID-19.'
    }
    if (areDinersSameHouseHold) {
      if (numChildren > 4) {
        return 'Can\'t have more than 4 children 12 and under in a group';
      }
    }
    if (numChildren === 2) {
      if (numDiners === 3) {
        return 'Children can\'t take up for than half the group if diners are from different households.';
      }
    }
    return;
  }

  console.log(whyCannot());

  const canDineIn = () => {
    if (numDiners <= 2) {
      return true;
    }
    if (numDiners >= 6) {
      return false;
    }
    if (!isEveryAbove12FullyVaccinatedOrEligible) {
      return false;
    }
    if (numChildren === 0) {
      return true;
    }
    if (areDinersSameHouseHold) {
      if (numChildren <= 4) {
        return true;
      }
      return false;
    }
    if (numChildren === 1) {
      return true;
    } else if (numChildren === 2) {
      if (numDiners === 3) {
        return false;
      } else if (numDiners === 4 || numDiners === 5) {
        return true;
      }
    }
  }

  const renderPersonImg = (person: Person, index: number) => {
    const { isChild, vaccinationStatus } = person;
    let statusIcon;
    switch (vaccinationStatus) {
      case VaccinationStatus.None:
        statusIcon = <img src={notVaccinated} />;
        break;
      case VaccinationStatus.Partially:
        statusIcon = <img src={partiallyVaccinated} />;
        break;
      case VaccinationStatus.Fully:
        statusIcon = <img src={fullyVaccinated} />;
        break;
      case VaccinationStatus.Recovered:
        statusIcon = <img src={recovered} />;
        break;
      case VaccinationStatus.NegativeTest:
        statusIcon = <img src={negativeTest} />;
        break;
    }


    return (
      <div key={index} className="person">
        <div className="status-icons">
          {statusIcon}
        </div>
        <div className="human">
          {isChild ? <img className="child" src={child} /> : <img className="adult" src={adult} />}
        </div>
      </div>
    );
  }

  const updateIsChild = (personIndex: number) => {
    const newPeople = people.map((p, i) => {
      if (i === personIndex) {
        const updatedPerson = {
          ...p,
          isChild: !p.isChild
        }
        return updatedPerson;
      }
      return p;
    })
    setPeople(newPeople);
  }

  const updateVaccinationStatus = (personIndex: number, status: VaccinationStatus) => {
    const newPeople = people.map((p, i) => {
      if (i === personIndex) {
        const updatedPerson = {
          ...p,
          vaccinationStatus: status
        }
        return updatedPerson;
      }
      return p;
    })
    setPeople(newPeople);
  }

  const renderPerson = (person: Person, idx: number) => {
    return (
      <div key={idx}>
        <h2>Person {idx + 1} </h2>
        <input type="checkbox" id={`isChild-${idx}`} name={`isChild-${idx}`}
          checked={person.isChild}
          onChange={() => updateIsChild(idx)}></input>
        <label htmlFor={`isChild-${idx}`}>Is a Child. 12 or Below.</label>
        <br></br>
        <div>
          Vaccinated?
          <input type="radio" id={`radio-no-vac-${idx}`}
            name={`vaccination-status-${idx}`} value={VaccinationStatus.None}
            checked={person.vaccinationStatus === VaccinationStatus.None}
            onChange={(e: ChangeEvent<HTMLInputElement>) =>
              updateVaccinationStatus(idx, e.target.value as VaccinationStatus)}
          />
          <label htmlFor={`radio-no-vac-${idx}`}>No</label>
          <input type="radio" id={`radio-part-vac-${idx}`}
            name={`vaccination-status-${idx}`} value={VaccinationStatus.Partially}
            checked={person.vaccinationStatus === VaccinationStatus.Partially}
            onChange={(e: ChangeEvent<HTMLInputElement>) =>
              updateVaccinationStatus(idx, e.target.value as VaccinationStatus)}
          />
          <label htmlFor={`radio-part-vac-${idx}`}>Partially</label>
          <input type="radio" id={`radio-full-vac-${idx}`}
            name={`vaccination-status-${idx}`} value={VaccinationStatus.Fully}
            checked={person.vaccinationStatus === VaccinationStatus.Fully}
            onChange={(e: ChangeEvent<HTMLInputElement>) =>
              updateVaccinationStatus(idx, e.target.value as VaccinationStatus)}
          />
          <label htmlFor={`radio-full-vac-${idx}`}>Fully (including 2 weeks wait)</label>
          <br></br>
          <input type="radio" id={`radio-recovered-${idx}`}
            name={`vaccination-status-${idx}`} value={VaccinationStatus.Recovered}
            checked={person.vaccinationStatus === VaccinationStatus.Recovered}
            onChange={(e: ChangeEvent<HTMLInputElement>) =>
              updateVaccinationStatus(idx, e.target.value as VaccinationStatus)}
          />
          <label htmlFor={`radio-recovered-${idx}`}>Recovered from Covid-19 in the past 270 days</label>
          <br></br>
          <input type="radio" id={`radio-negative-${idx}`}
            name={`vaccination-status-${idx}`} value={VaccinationStatus.NegativeTest}
            checked={person.vaccinationStatus === VaccinationStatus.NegativeTest}
            onChange={(e: ChangeEvent<HTMLInputElement>) =>
              updateVaccinationStatus(idx, e.target.value as VaccinationStatus)}
          />
          <label htmlFor={`radio-negative-${idx}`}>Negative pre-event test result</label>
        </div>
      </div >
    );
  }

  const canText = () => {
    if (numDiners === 1) {
      if (people[0].isChild) {
        return [
          'Where your parents lol',
          'So young independent wor.',
          'Eating macs ah'
        ];
      }
      return [
        'Hello can see the word \'together\' or not',
        <>Do you need a dining partner? Contact me <a href="https://www.youtube.com/watch?v=dQw4w9WgXcQ">at</a></>
      ];
    }
    return ['Can dine in together'];
  }

  return (
    <div>
      <h1>
        {'Can you Dine-in Together? '}
      </h1>
      <h1>
        {canDineIn() ? <img src={can} /> : <img src={cannot} />}
        {canDineIn() ? canText()[Math.floor(Math.random() * canText().length)] : 'Cannot. Sad'}
      </h1>
      {canDineIn() === false &&
        <>
          <i>Why leh?</i>
          <h2>{whyCannot()}</h2>
        </>}
      <hr></hr>
      <img className="legend" src={legend} />
      <div className="people">
        {people.map(renderPersonImg)}
      </div>
      <button onClick={addPerson}>Add person</button>
      {people.length > 1 && <button onClick={removePerson}>Remove person</button>}
      <div>
        <br></br>
        <input type="checkbox" id="sameHousehold" name="sameHousehold"
          checked={areDinersSameHouseHold}
          onChange={() => { setAreDinersSameHousehold(!areDinersSameHouseHold) }}></input>
        <label htmlFor="sameHousehold">Is everyone from same household?</label>
      </div>
      {people.map(renderPerson)}

    </div>
  );
}

export default Stupid;
